import React, { useState, useContext } from 'react';
import { motion } from 'framer-motion';
import { AppContext } from '../../contexts/AppContext';
import { lightTheme, darkTheme } from '../../utils/theme';
import { CameraIcon } from '@heroicons/react/24/outline';

const BabyIllustration = ({ theme }) => {
  return (
    <div style={{ marginBottom: '1.5rem', textAlign: 'center' }}>
      <svg width="80" height="80" viewBox="0 0 100 100" xmlns="http://www.w3.org/2000/svg">
        <circle cx="50" cy="50" r="45" fill={theme.isDarkMode ? theme.accentColorSoft : theme.primaryColorSoft || '#e0f7fa'} />
        <text x="50" y="60" fontSize="40" textAnchor="middle" fill={theme.textColor}>😴</text>
      </svg>
    </div>
  );
};

const BabyProfileScreen = ({ onNext }) => {
  const { state, dispatch } = useContext(AppContext);
  const currentTheme = state.isDarkMode ? darkTheme : lightTheme;
  const [babyName, setBabyName] = useState(state.babyName || '');
  const [babyAgeMonths, setBabyAgeMonths] = useState(state.babyAgeMonths || '');
  const [babyPhoto, setBabyPhoto] = useState(null);
  const [babyPhotoName, setBabyPhotoName] = useState('');
  // Debug state removed: const [debugInfo, setDebugInfo] = useState('');

  const processDataAndNavigate = () => {
    // Debug logging removed
    const trimmedName = babyName.trim();
    const trimmedAgeMonths = String(babyAgeMonths).trim();

    if (trimmedName === '' || trimmedAgeMonths === '') {
      // Consider a more user-friendly error display than alert if needed in future
      alert('Please enter both baby\'s name and age.');
      return;
    }
    
    const age = parseInt(trimmedAgeMonths, 10);

    if (isNaN(age) || age < 0 || age > 120) {
      alert('Please enter a valid age for your baby (0-120 months).');
      return;
    }

    dispatch({ type: 'SET_BABY_PROFILE', payload: { name: trimmedName, ageMonths: age, photo: babyPhotoName } });
    
    const onNextData = { babyName: trimmedName, babyAgeMonths: age, babyPhotoName };
    console.log("BabyProfileScreen: Calling onNext with:", onNextData);
    onNext(onNextData);
  };

  const handleSubmit = (e) => {
    if (e) e.preventDefault();
    // Debug logging removed
    processDataAndNavigate();
  };

  const handleButtonClick = () => {
    // Debug logging removed
    processDataAndNavigate();
  };

  const screenVariants = {
    hidden: { opacity: 0, x: 50 },
    visible: { opacity: 1, x: 0, transition: { duration: 0.5, ease: "easeOut" } },
    exit: { opacity: 0, x: -50, transition: { duration: 0.4, ease: "easeIn" } },
  };

  const inputStyle = {
    borderColor: currentTheme.cardBorderColor,
    backgroundColor: currentTheme.inputBackgroundColor || currentTheme.cardBackgroundColor,
    color: currentTheme.textColor,
  };
  
  const handlePhotoUpload = (event) => {
    if (event.target.files && event.target.files[0]) {
      const file = event.target.files[0];
      setBabyPhoto(file);
      setBabyPhotoName(file.name);
      console.log("Selected photo:", file.name);
    }
  };

  const pageStyle = {
    backgroundColor: currentTheme.pageBackground, 
    backgroundImage: state.isDarkMode 
      ? 'radial-gradient(circle at top left, rgba(42, 59, 77, 0.5) 0%, transparent 30%), radial-gradient(circle at bottom right, rgba(62, 80, 98, 0.5) 0%, transparent 30%)'
      : 'radial-gradient(circle at top left, rgba(178, 235, 242, 0.3) 0%, transparent 40%), radial-gradient(circle at bottom right, rgba(224, 247, 250, 0.4) 0%, transparent 40%)',
    color: currentTheme.textColor,
    fontFamily: currentTheme.fontFamilyBody,
    minHeight: '100vh',
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    padding: '1.5rem',
    position: 'relative',
  };

  return (
    <motion.div
      variants={screenVariants}
      initial="hidden"
      animate="visible"
      exit="exit"
      className="flex flex-col items-center justify-between w-full h-full"
      style={pageStyle}
    >
      <div className="w-full max-w-md mb-4 text-center">
        <p className="text-sm" style={{ color: currentTheme.subtleTextColor }}>Step 3 of 6: Your Little One</p>
        <div className="w-full bg-gray-200 rounded-full h-1.5 mt-1 dark:bg-gray-700">
          <div className="h-1.5 rounded-full" style={{ width: '50%', backgroundColor: currentTheme.primaryColor }}></div>
        </div>
      </div>

      {/* Debug DIV removed */}

      <div className="flex flex-col items-center w-full">
        <BabyIllustration theme={currentTheme} />
        <motion.h2 
          className="text-2xl md:text-3xl font-semibold mb-2 text-center"
          style={{ fontFamily: currentTheme.fontFamilyHeadings, color: currentTheme.headingColor }}
        >
          Tell Us About Your Little One
        </motion.h2>
        <p className="text-md mb-6 text-center" style={{ color: currentTheme.subtleTextColor }}>
          Let's personalize sleep guidance for your little one.
        </p>

        <form onSubmit={handleSubmit} className="w-full max-w-md">
          <div className="mb-4">
            <label htmlFor="babyName" className="block mb-1.5 text-sm font-medium" style={{ color: currentTheme.textColor }}>
              Baby's Name
            </label>
            <input 
              type="text" 
              id="babyName" 
              value={babyName}
              onChange={(e) => setBabyName(e.target.value)}
              placeholder="E.g., Millie, Leo"
              className="w-full p-3 border rounded-lg shadow-sm focus:ring-2 focus:ring-opacity-50"
              style={inputStyle}
              required
            />
          </div>

          <div className="mb-4">
            <label htmlFor="babyAgeMonths" className="block mb-1.5 text-sm font-medium" style={{ color: currentTheme.textColor }}>
              Baby's Age (in months)
            </label>
            <input 
              type="number" 
              id="babyAgeMonths" 
              value={babyAgeMonths}
              onChange={(e) => setBabyAgeMonths(e.target.value)}
              placeholder="E.g., 6"
              className="w-full p-3 border rounded-lg shadow-sm focus:ring-2 focus:ring-opacity-50"
              style={inputStyle}
              required
            />
            <p className="text-xs mt-1.5" style={{ color: currentTheme.subtleTextColor }}>
              This helps us provide age-appropriate sleep recommendations.
            </p>
          </div>
          
          <div className="mb-6">
            <label htmlFor="babyPhoto" className="block mb-1.5 text-sm font-medium" style={{ color: currentTheme.textColor }}>
              Optional: Upload Baby Photo
            </label>
            <div 
              className="w-full p-3 border rounded-lg shadow-sm flex items-center justify-center cursor-pointer hover:bg-opacity-50"
              style={{ ...inputStyle, backgroundColor: currentTheme.isDarkMode ? 'rgba(255,255,255,0.05)' : 'rgba(0,0,0,0.02)', borderColor: currentTheme.borderColor }}
              onClick={() => document.getElementById('babyPhotoInput').click()}
            >
              <CameraIcon className="h-5 w-5 mr-2" style={{ color: currentTheme.subtleTextColor }} />
              <span className="text-sm" style={{ color: currentTheme.subtleTextColor }}>
                {babyPhotoName ? babyPhotoName : 'Choose a photo'}
              </span>
            </div>
            <input 
              type="file" 
              id="babyPhotoInput" 
              accept="image/*" 
              onChange={handlePhotoUpload}
              className="hidden"
            />
          </div>

          <motion.button
            type="submit"
            onClick={handleButtonClick} // Retaining onClick as a robust measure
            whileHover={{ scale: 1.05 }}
            whileTap={{ scale: 0.95 }}
            className="w-full px-8 py-3.5 rounded-full font-semibold text-white text-lg transition-colors shadow-xl"
            style={{ backgroundColor: currentTheme.ctaColor, color: currentTheme.ctaTextColor || 'white' }}
          >
            Continue
          </motion.button>
        </form>
      </div>
      
      <div style={{ flexGrow: 1 }}></div> 

    </motion.div>
  );
};

export default BabyProfileScreen;

