import React from 'react';

// Placeholder for a more sophisticated baby avatar component
const BabyAvatarDisplay = ({ avatarUrl, babyName }) => {
  return (
    <img 
      src={avatarUrl || '/images/default_baby_avatar.png'} // Fallback to a default path
      alt={`${babyName || 'Baby'}'s avatar`}
      className="w-16 h-16 md:w-20 md:h-20 rounded-full shadow-md border-2 border-white object-cover"
    />
  );
};

const TimeOfDayBanner = ({ userName, babyName, timeOfDay, babyAvatarUrl }) => {
  let greeting = "Hello";
  let timeIcon = "☀️"; // Default icon

  switch (timeOfDay) {
    case 'morning':
      greeting = "Good morning";
      timeIcon = "☀️";
      break;
    case 'afternoon':
      greeting = "Good afternoon";
      timeIcon = "🌤️";
      break;
    case 'evening':
      greeting = "Good evening";
      timeIcon = "🌙";
      break;
    default:
      greeting = "Welcome back";
      break;
  }

  return (
    <div 
      className={`p-4 md:p-6 rounded-lg shadow-lg mb-6 flex flex-col sm:flex-row items-center justify-between 
        ${timeOfDay === 'morning' ? 'bg-gradient-to-r from-yellow-300 to-orange-300 text-orange-800' : 
          timeOfDay === 'afternoon' ? 'bg-gradient-to-r from-sky-300 to-blue-400 text-blue-800' : 
          'bg-gradient-to-r from-indigo-400 to-purple-500 text-purple-100'}
      `}
    >
      <div className="text-center sm:text-left mb-4 sm:mb-0">
        <h1 className="text-2xl md:text-3xl font-bold">
          {greeting}, {userName || 'Parent'}! {timeIcon}
        </h1>
        <p className="text-md md:text-lg mt-1">
          Here's what's new for {babyName || 'your little one'}.
        </p>
      </div>
      <div className="flex-shrink-0">
        <BabyAvatarDisplay avatarUrl={babyAvatarUrl} babyName={babyName} />
      </div>
    </div>
  );
};

export default TimeOfDayBanner;

