import React from 'react';

const AchievementsCard = ({ achievements }) => {
  if (!achievements || achievements.length === 0) {
    return (
      <div className="bg-white p-4 rounded-lg shadow">
        <h2 className="text-xl font-semibold text-gray-700 mb-2">Achievements</h2>
        <p className="text-gray-500">No achievements unlocked yet. Keep logging to see your progress!</p>
      </div>
    );
  }

  return (
    <div className="bg-white p-4 rounded-lg shadow">
      <h2 className="text-xl font-semibold text-gray-700 mb-3">Achievements Unlocked! 🏅</h2>
      <div className="space-y-3">
        {achievements.map(ach => (
          <div key={ach.id} className={`p-3 rounded-md flex items-center ${ach.unlocked ? 'bg-green-50 border border-green-200' : 'bg-gray-50 border border-gray-200'}`}>
            <span className="text-4xl mr-3">{ach.icon || '🏆'}</span> {/* Changed from text-2xl to text-4xl */}
            <div>
              <h3 className={`font-semibold ${ach.unlocked ? 'text-green-700' : 'text-gray-600'}`}>{ach.name}</h3>
              {ach.description && <p className={`text-sm ${ach.unlocked ? 'text-green-600' : 'text-gray-500'}`}>{ach.description}</p>}
              {!ach.unlocked && <p className="text-xs text-gray-400 italic">Keep going!</p>}
            </div>
          </div>
        ))}
      </div>
      {/* Placeholder for progress towards next milestones */}
      <p className="text-xs text-gray-400 mt-3 italic">More milestones coming soon...</p>
    </div>
  );
};

export default AchievementsCard;

